#home-container {
  width: 100%;
  height: 100%;
}

.highlight.homepage {
  text-decoration: underline;
}

#greeting-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

#greeting {
  color: rgb(255, 255, 255);
  font-family: "Poppins", sans-serif;
  font-size: 3.2em;
}

#greeting-intro {
  font-size: 2em;
  font-family: "Josefin Sans", sans-serif;
  color: rgb(255, 255, 255);
}

#selfie {
  width: 8em;
  margin: 5em auto;
}

#home-container .curved {
  font-size: 0;
}

#scroll-down-icon {
  width: 3em;
  z-index: 3;
  margin: 7em 0 -7em;
}

@media only screen and (max-width: 764px) {

  #home-container {
    height: 70vh;
  }

  #greeting-container {
    margin-top: 5em;
  }

  #greeting {
    font-size: 1.7em;
  }

  #greeting-intro {
    font-size: 1.4em;
  }

  #selfie {
    width: 8em;
    margin: 5em auto 1em;
  }

  #scroll-down-icon {
    display: none;
  }

  #home-container .curved {
    font-size: 0;
  }

}