#about-container {
  background-color: transparent;
  width: 75%;
  height: 100%;
  padding: 2em 5em 5em 5em;
  margin: 0 auto;
  text-align: left;
}

#about-section {
  width: 100%;
}

#about-me-intro {
  font-family: "Josefin Sans", sans-serif;
  color: whitesmoke;
  font-size: 16px;
}

@media only screen and (max-width: 764px) {

  #about-container {
    width: 100%;
    padding: 1em;
  }

}
