#contact {
  background-color: rgb(18, 20, 22);
}

#contact-container {
  width: 600px;
  margin: 2em auto 2em;
}

#contact-container .section-title {
  color: rgb(255, 255, 255);
}

#contact-section {
  padding: 1em;
  background-color: rgb(0, 0, 0);
  border-radius: 0.625em;
  font-family: "Josefin Sans", sans-serif;
}

#contact-subtitle {
  font-size: 1em;
  font-family: "Josefin Sans", sans-serif;
  color: rgb(255, 255, 255);
  margin: -1.5em 0 2em;
}

.label.contact {
  color: rgb(255, 255, 255);
}

.form-fields {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  margin-bottom: 1em;
}

#message-input {
  height: 12.5em;
}

#contact-submit {
  font-family: "Josefin Sans";
  color: white;
  transition: 0.3s;
}

#contact-submit:hover {
  font-family: "Josefin Sans";
  color: rgb(46, 196, 138);
}

.input, .textarea {
  background-color : rgb(18, 20, 22);; 
  color: rgb(46, 196, 138);
}

.curved svg {
  background-color: black;
}

@media only screen and (max-width: 764px) {

  #contact-container {
    width: 100%;
    padding: 1em;
  }

  #contact-subtitle {
    font-size: 1em;
    font-family: "Josefin Sans", sans-serif;
    color: rgb(255, 255, 255);
    margin: -1em 0 2em;
  }

}